import { ProductsService } from './../products.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-product-manage',
  templateUrl: './product-manage.component.html',
  styleUrls: ['./product-manage.component.scss']
})
export class ProductManageComponent implements OnInit {

  constructor(
    private productsService: ProductsService
  ) { }
  isLoading = false;
  categoryForm: FormGroup;
  subcategoryForm: FormGroup;
  categories: {id:string, title: string, description: string}[];
  subcategories: {id:string, title: string, description: string}[];
  userIsAuthenticated = true;

  ngOnInit(): void {
    this.productsService.getCategories().subscribe(docs => {
      this.isLoading = false;
      this.categories = docs;
    }, err => {
      console.log(err);
    });
    this.categoryForm = new FormGroup({
      title: new FormControl('', { validators: [ Validators.required ] }),
      description: new FormControl('')
    });

    this.subcategoryForm = new FormGroup({
      title: new FormControl('', { validators: [ Validators.required ] }),
      description: new FormControl('')
    });
  }

  onSaveCategory(){
    if (this.categoryForm.invalid) {
      return;
    }
    // logic
    this.productsService.addCategory(
      this.categoryForm.value.title,
      this.categoryForm.value.description
    ).subscribe(
      res => {
        this.isLoading = false;
      }, err => console.log(err)
    );
  }

  onSaveSubcategory(){
    if (this.subcategoryForm.invalid) {
      return;
    }
    this.productsService.addSubcategory(
      this.subcategoryForm.value.title,
      this.subcategoryForm.value.description
    ).subscribe(
      res => {
        this.isLoading = false;
      }, err => console.log(err)
    )
  }
}
