import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { DialogForProductSpecificationComponent } from './dialog-for-product-specification.component';
import { Subscription, Observable } from 'rxjs';
import { ProductsService } from './../products.service';
import { Component, OnInit, OnDestroy, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormControl, Validators, FormArray, FormBuilder } from '@angular/forms';
import { ActivatedRoute, ParamMap, Router } from '@angular/router';
import { ProductCreateForm } from '../product-create-form.model';
import { MatDialog } from '@angular/material/dialog';
import { MatAutocomplete, MatAutocompleteSelectedEvent } from '@angular/material/autocomplete';
import { map, startWith, switchMap } from 'rxjs/operators';
import { MatChipInputEvent } from '@angular/material/chips';

@Component({
  selector: 'app-product-create',
  templateUrl: './product-create.component.html',
  styleUrls: ['./product-create.component.scss']
})
export class ProductCreateComponent implements OnInit, OnDestroy {

  formatPS = (obj) => JSON.stringify(obj);
  separatorKeysCodes: number[] = [ENTER, COMMA];
  tagInputCtrl = new FormControl();
  filteredTags: Observable<any>;
  selectedTags: string[] = [];
  @ViewChild('tagInput') tagInput: ElementRef<HTMLInputElement>;
  @ViewChild('auto') matAutocomplete: MatAutocomplete;
  getKeys = (obj) => { return Object.keys(obj) };
  productSpecifications: {product: any, productSpecific: any}[];  // product field contains populated data (during mongoose)
  syncProductsSubscription: Subscription;

  get productPhotos(): FormArray {
    return this.productForm.get('photos') as FormArray;
  };

  providedPS: any;
  isLoading = false;
  formMode: number;
  productForm: FormGroup;
  productSupportOptions = [];
  detectProductPhotos(event: any) {
    const files = event.target.files;
    if (files) {
      for (var i = 0; i < files.length; i++) {
        let file = files[i];
        let reader = new FileReader();
        reader.onload = (e) => {
          this.productPhotos.push(
            this.formBuilder.group({
              file,
              url: e.target.result  //Base64 string for preview image
            })
          );
          if ( i+1 === files.length ){ // Empty files input after read
            event.target.value = '';
          }
        }
        reader.readAsDataURL(file);
      }
    }
  }


  constructor(
    public route: ActivatedRoute,
    private router: Router,
    private productsService: ProductsService,
    private formBuilder: FormBuilder,
    public dialog: MatDialog
  ) {

  }
  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    // Add our fruit
    if ((value || '').trim()) {
      this.selectedTags.push(value.trim());
    }

    // Reset the input value
    if (input) {
      input.value = '';
    }

    this.tagInputCtrl.setValue(null);
  }

  remove(fruit: string): void {
    const index = this.selectedTags.indexOf(fruit);

    if (index >= 0) {
      this.selectedTags.splice(index, 1);
    }
  }

  selected(event: MatAutocompleteSelectedEvent): void {
    this.selectedTags.push(event.option.viewValue);
    this.tagInput.nativeElement.value = '';
    this.tagInputCtrl.setValue(null);
  }


  ngOnInit(): void {

    this.filteredTags = this.tagInputCtrl.valueChanges.pipe(
      startWith(null),
      switchMap((filterText: string | null) => {
        // convert the regular text to tag format
        const formatedText = filterText ? filterText.split(' ').join('-') : '';
        return this.productsService.getProductTags(formatedText);
      }));
    // ***********************************************//
    // Get table data
    // Do it each time data is received from socket
    this.syncProductsSubscription = this.productsService.syncProducts()
    .subscribe(data => {
      console.log(data)
      this.isLoading = true;
      this.productsService.getProductSpecifications().subscribe(docs => {
        this.isLoading = false;
        this.productSpecifications = docs;
      }, err => {
        console.log(err);
      });
    });
    // sync table data realtime

    this.productsService.getProductSpecifications().subscribe(docs => {
      this.isLoading = false;
      this.productSpecifications = docs;
    }, err => {
      console.log(err);
    });

// *************************************************//
// *************************************************//
//  Initialize productForm
    this.productForm = new FormGroup({
      title: new FormControl('', { validators: [ Validators.required ] }),
      description: new FormControl(null, { validators: [ Validators.required ]}),
      brand: new FormControl(null, { validators: [ Validators.required ] }),
      model: new FormControl(null, { validators: [ Validators.required ] }),
      detail: new FormControl('Starting content', { validators: [ Validators.required ] }),
      tags: new FormControl([]),
      support: new FormControl([]),

      specification: new FormControl({}),
      upc: new FormControl(null, { validators: [ Validators.required ] }),
      salePrice: new FormControl(null),

      location: new FormControl(null),
      addingAmount: new FormControl(null),
      costPerUnit: new FormControl(null),
      //
      photos: new FormArray([]),
    });
    this.productsService.getProductSupports().subscribe(productSupports => {
      this.productSupportOptions = productSupports;
    });
    // queryParams possible:  ?formMode=0 || 1 || 2&productSpecficationId=434ted4yet
    this.route.queryParamMap.subscribe((params) => {
      this.formMode = +params.get('formMode');
      if (params.has('productSpecficationId')) {
        this.productsService.getProductSpecificationById( params.get('productSpecficationId') ).subscribe(
          ps => {
            this.providedPS = ps;
          }, err => console.log(err)
        );
      }
    });
  }

  ngOnDestroy(){
    this.syncProductsSubscription.unsubscribe();
  }

  onSaveProduct(){
    if (this.productForm.invalid) {
      return;
    }
    //this.isLoading = true;
    const newProductData: ProductCreateForm = {
      title: this.productForm.value.title,
      description: this.productForm.value.description,
      brand: this.productForm.value.brand,
      model: this.productForm.value.model,
      detail: this.productForm.value.detail,
      tags: this.productForm.value.tags,
      support: this.productForm.value.support,

      specification: this.productForm.value.specification,
      upc: this.productForm.value.upc,
      salePrice: this.productForm.value.salePrice,

      location: this.productForm.value.location,
      addingAmount: this.productForm.value.addingAmount,
      costPerUnit: this.productForm.value.costPerUnit,

      photos: this.productForm.value.photos
    }
    this.productsService.addProduct(newProductData)
    .subscribe(
      res => {
        console.log(res);
        this.isLoading = false;
      }, err => console.log(err)
    );
    //this.form.reset();
  }

  removeSpecificationKey(k: string){
    const specification = this.productForm.value.specification;
    delete specification[k];
    this.productForm.patchValue({ specification: specification });
  }

  openSpecificationDialog(specName?: string): void {
    const dialogRef = this.dialog.open(DialogForProductSpecificationComponent, {
      autoFocus: false,
      disableClose: true,
      width: '250px',
      data: { value: {
        specName: specName ? specName : '',
        specValue: ''
      } }
    });

    dialogRef.afterClosed().subscribe((result:{specName: string; specValue: string}) => {
      if (result){
        const specification = this.productForm.value.specification;
        specification[result.specName] = result.specValue;
        this.productForm.patchValue({ specification: {...specification} });
      }

    });
  }
}

