import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, Inject, AfterViewInit, ViewChild, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

export interface DialogData {
  value: {
    specName: string;
    specValue: string;
  };
}

@Component({
  selector: 'dialog-for-product-specification',
  templateUrl: './dialog-for-product-specification.component.html',
})

export class DialogForProductSpecificationComponent implements AfterViewInit, OnInit {

  constructor(
    public dialogRef: MatDialogRef<DialogForProductSpecificationComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  specForm: FormGroup;
  @ViewChild('specNameInput') specNameInput;
  @ViewChild('specValueInput') specValueInput;

  onNoClick(): void {
    this.dialogRef.close();
  }
  onSubmit(){
    if (this.specForm.invalid) {
      return;
    }
    this.dialogRef.close({
      specName: this.specForm.value.name,
      specValue: this.specForm.value.value
    });
  }
  ngOnInit(){
    this.specForm = new FormGroup({
      name: new FormControl(this.data.value.specName, { validators: [ Validators.required ] }),
      value: new FormControl(this.data.value.specValue, { validators: [ Validators.required ] })
    });


    // setTimeout is needed to work. The focus action is applied on the native element
    // (it cannot be allowed at iniatiation time at all. None of the digest cycles like ngAfterViewInit works)
    if (this.data.value.specName) {
      setTimeout(() => this.specValueInput.nativeElement.focus());
    } else {
      setTimeout(() => this.specNameInput.nativeElement.focus());
    }
  }
  ngAfterViewInit(){

  }
}
