import { Post } from './../post.model';
import { PostsService } from './../posts.service';
import { mimeType } from './mime-type.validator';
import { Component, EventEmitter, Output, OnInit, SecurityContext } from '@angular/core';

import { NgForm, FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, ParamMap, Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-post-create',
  templateUrl: './post-create.component.html',
  styleUrls: ['./post-create.component.scss']
})
export class PostCreateComponent implements OnInit {

  enteredTitle = '';
  enteredContent = '';
  form: FormGroup;
  imagePreview;
  isLoading = false;
  private post: Post;
  //@Output() postCreated = new EventEmitter<Post>();

  constructor(
    public postsService: PostsService,
    public route: ActivatedRoute,
    private router: Router,
    private sanitizer: DomSanitizer
    ) { }

  ngOnInit(): void {
    this.form = new FormGroup({
      title: new FormControl(null, { validators: [ Validators.required, Validators.minLength(3) ] }),
      content: new FormControl(null, { validators: [ Validators.required ]}),
      image: new FormControl(null, { validators: [  ], asyncValidators: [mimeType] }),
      imagePath: new FormControl(null)
    });
    this.route.paramMap.subscribe((paramMap: ParamMap) => {
      if (paramMap.has('postId')) {
        this.isLoading = true;
        this.postsService.getPost( paramMap.get('postId') ).subscribe(
          post => {
            this.isLoading = false;
            this.post = post;
            this.form.patchValue({
              title: post.title,
              content: post.content,
              imagePath: post.imagePath
            });
          }, err => console.log(err)
        );
      } else {
        this.post = null;
      }
    });
  }

  onSavePost = function(){
    if (this.form.invalid) {
      return;
    }
    this.isLoading = true;
    if (!this.post) {
          // No postId -> Add post
          // Create new post. Image needs to be required but currently can be empty
          this.postsService.addPost(
            this.form.value.title,
            this.form.value.content,
            this.form.value.image)
          .subscribe(
            res => {
              console.log(res);
              this.isLoading = false;
              this.router.navigate(['/']);
            }, err => console.log(err)
          );
    } else {
          // Update existing post. Image field is optional, and its value determines
          // whether the payload of postsService.updatePost will contain image property
          this.postsService.updatePost(
            this.post.id,
            this.form.value.title,
            this.form.value.content,
            this.form.value.image).subscribe(
            res => {
              console.log(res);
              this.isLoading = false;
              this.router.navigate(['/']);
            }, err => {
              console.log(err);
            }
          );
    }
    this.form.reset();
  }

  onImagePicked(event: Event) {
    const reader = new FileReader();
    const file = (event.target as HTMLInputElement).files[0];
    if(!file){
      this.form.patchValue({ image: null });
      this.imagePreview = '';
    } else {
      this.form.patchValue({ image: file });
      console.log(this.form.value)
      reader.readAsDataURL(file);
    }

    this.form.get('image').updateValueAndValidity();

    reader.onload = () => {
      //this.imagePreview = (reader.result as string);
      this.imagePreview = this.sanitizer.bypassSecurityTrustUrl(reader.result as string);
      //this.imagePreview = this.sanitizer.sanitize(SecurityContext.STYLE, reader.result as string);
      //this.sanitizer.sanitize(SecurityContext.STYLE, 'url(' + this.image + ')');
    };

  }
}
