import { AuthService } from './../auth/auth.service';
import { Post } from './post.model';
import { HttpClient } from '@angular/common/http'
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

import { Subject, Observable, Observer, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';

import * as socketIo from 'socket.io-client';
import { EnvService } from '../env.service';
interface Socket {
  on(event: string, callback: (data: any) => void );
  emit(event: string, data: any);
}
declare var io : {
  connect(url: string): Socket;
};



@Injectable({providedIn: 'root'})
export class PostsService {
  //private posts: Post[] = [];
  private postsSocket;
  socket: Socket;
  observer: Observer<any>;
  syncPosts(): Observable<any> {
    // Check whether postsSocket is already initiated, if not: open new socket
    if (!this.postsSocket) {
      this.postsSocket = socketIo( environment.apiBaseUrl + '/PostsCollection');
    }
    this.postsSocket.on('data', (res) => {
      this.observer.next(res.data);
    });

    return new Observable(observer => {
      this.observer = observer;
    });
  }

  constructor(
    private envService: EnvService,
    private authService: AuthService,
    private http: HttpClient) {}

  getPosts(postsPerPage: number, currentPage: number) {
    const queryParams = `?pagesize=${postsPerPage}&page=${currentPage}`;
    return this.http.get<{message: string, posts: any, postCount: number}>( environment.apiBaseUrl + '/api/posts' + queryParams)
    .pipe(map((postData): { postCount: number, posts: Post[] } => {
      return {
        postCount: postData.postCount,
        posts: postData.posts.map((post) => {
          post.id = post._id;
          return post;
        })
      };
    }));
  }

  addPost(title: string, content: string, image: File) {
    const postData = new FormData();
    postData.append('title', title);
    postData.append('content', content);
    postData.append('image', image, image.name);//MARK2
    return this.http.post<{message: string, postId: string}>( environment.apiBaseUrl + '/api/posts', postData)
    .pipe(map(responseData => {
      return "something cool";
    }));
  }

  deletePost(postId: string) {
    return this.http.delete( environment.apiBaseUrl + '/api/posts/' + postId)
    .pipe(
      catchError(err => {
        if (err.error && err.error.needLogout){
          console.log(err)
          this.authService.logoutUser(true).subscribe(res => {
            console.log(res)
          });
        }
        return throwError(err);
      }),
      map((data) => {
        return data;
      }
    ));
  }

  getPost(id: string){
    return this.http.get<any>( environment.apiBaseUrl + '/api/posts/' + id)
      .pipe(map((postData): Post => {
        postData.id = postData._id;
        return postData;
      }));
  }

  updatePost(id: string, title: string, content: string, image?: File) {
    const postData = new FormData();
    postData.append('title', title);
    postData.append('content', content);
    if (image){
      postData.append('image', image, image.name);//MARK2
    }
    return this.http.put<{message: string }>( environment.apiBaseUrl + '/api/posts/' + id, postData)
    .pipe(
      catchError(err => {
        if (err.error && err.error.needLogout){
          console.log(err)
          this.authService.logoutUser(true).subscribe(res => {
            console.log(res)
          });
        }
        return throwError(err);
      }),
      map((data) => {
        return data;
      }
    ));
  }
}
