import { AuthService } from './../auth/auth.service';
import { HttpClient, HttpHeaders } from '@angular/common/http'
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

import { Subject, Observable, Observer, throwError } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { ProductCreateForm } from './product-create-form.model';
import * as socketIo from 'socket.io-client';
import { EnvService } from '../env.service';
interface Socket {
  on(event: string, callback: (data: any) => void );
  emit(event: string, data: any);
}
declare var io : {
  connect(url: string): Socket;
};


@Injectable({providedIn: 'root'})
export class ProductsService {

  private productsSocket;
  socket: Socket;
  observer: Observer<any>;
  constructor(
    private envService: EnvService,
    private authService: AuthService,
    private http: HttpClient) {}

  syncProducts(): Observable<any> {
    // Check whether postsSocket is already initiated, if not: open new socket
    if (!this.productsSocket) {
      this.productsSocket = socketIo( environment.apiBaseUrl + '/ProductsCollection');
    }
    this.productsSocket.on('data', (res) => {
      this.observer.next(res.data);
    });

    return new Observable(observer => {
      this.observer = observer;
    });
  }

  getProductTags(filterText?: string){
    return this.http.get<{message: string, productTags: any}>( environment.apiBaseUrl + '/api/products/product-tags' + (filterText ? '?filter-text='+filterText : ''))
    .pipe(map(ptData => {
      return ptData.productTags.map(pt => {
        pt.id = pt._id;
        return pt;
      });
    }));
  }
  getProductSupports(){
    //console.log(process.env)
    return this.http.get<{message: string, productSupports: any}>( environment.apiBaseUrl + '/api/products/product-supports')
    .pipe(map(psData => {
      return psData.productSupports;
    }));
  }
  getProductSpecificationById(psId: string){
    return this.http.get<any>( environment.apiBaseUrl + '/api/products/product-specifications/' + psId)
    .pipe(map((psData) => {
      psData.id = psData._id;
      return psData;
    }));
  }
  getProductSpecifications(){
    return this.http.get<{message: string, productSpecifications: any}>( environment.apiBaseUrl + '/api/products/product-specifications')
    .pipe(map((psData) => {
      return psData.productSpecifications.map((ps) => {
        ps.id = ps._id;
        return ps;
      })
    }));
  }
  getProducts() {
    return this.http.get<{message: string, products: any, productCount: number}>( environment.apiBaseUrl + '/api/products')
    .pipe(map((productData): {productCount: number, products: any } => {
      return {
        productCount: productData.productCount,
        products: productData.products.map((product) => {
          product.id = product._id;
          return product;
        })
      };
    }));
  }

  addProduct(newProduct: ProductCreateForm) {

    const productData = new FormData();
    const propertiesThatNeedStringify = ['support','specification','tags'];
    for (const key in newProduct) {
      if( key !== 'photos' ) {
        if( propertiesThatNeedStringify.indexOf(key) !== -1 ){
          productData.append(key, JSON.stringify(newProduct[key]));
        } else {
          productData.append(key, newProduct[key]);
        }
      }
    }

    for (let file of newProduct.photos){
      productData.append('photos', file.file, file.name);
    }
    return this.http.post<{message: string, productId: string}>( environment.apiBaseUrl + '/api/products', productData)
    .pipe(map(responseData => {
      return "something cool";
    }));
  }

  deleteProduct(productId: string) {
    return this.http.delete( environment.apiBaseUrl + '/api/produts/' + productId)
    .pipe(
      catchError(err => {
        if (err.error && err.error.needLogout){
          console.log(err)
          this.authService.logoutUser(true).subscribe(res => {
            console.log(res)
          });
        }
        return throwError(err);
      }),
      map((data) => {
        return data;
      }
    ));
  }

  getProduct(id: string){
    return this.http.get<any>( environment.apiBaseUrl + '/api/products/' + id)
      .pipe(map((productData) => {
        productData.id = productData._id;
        return productData;
      }));
  }

  updateProduct(id: string, title: string, description: string, brand: string, model: string, upc: string, detail: string, support: any) {
    const productData = new FormData();
    productData.append('title', title);
    productData.append('description', description);
    productData.append('brand', brand);
    productData.append('model', model);
    productData.append('upc', upc);
    productData.append('detail', detail);
    productData.append('support', JSON.stringify(support));
    return this.http.put<{ message: string }>( environment.apiBaseUrl + '/api/products/' + id, productData)
    .pipe(
      catchError(err => {
        if (err.error && err.error.needLogout){
          console.log(err)
          this.authService.logoutUser(true).subscribe(res => {
            console.log(res)
          });
        }
        return throwError(err);
      }),
      map((data) => {
        return data;
      }
    ));
  }

  addCategory(title: string, description: string){
    const categoryData = new FormData();
    categoryData.append('title', title);
    categoryData.append('description', description);
    return this.http.post<{message: string, categoryId: string}>( environment.apiBaseUrl + '/api/products/categories', categoryData)
    .pipe(map(responseData => {
      return responseData;
    }));
  }

  addSubcategory(title: string, description: string){
    const subcategoryData = new FormData();
    subcategoryData.append('title', title);
    subcategoryData.append('description', description);
    return this.http.post<{message: string, subcategoryId: string}>( environment.apiBaseUrl + '/api/products/subcategories', subcategoryData)
    .pipe(map(responseData => {
      return responseData;
    }));
  }

  getCategories(){
    return this.http.get<{message: string, categories: any[]}>( environment.apiBaseUrl + '/api/products/categories')
    .pipe(map((categoriesData) => {
      return categoriesData.categories.map((category) => {
          category.id = category._id;
          return category;
      });
    }));
  }

  getSubcategories(){
    return this.http.get<{message: string, subcategories: any[]}>( environment.apiBaseUrl + '/api/products/subcategories')
    .pipe(map((subcategoriesData) => {
      return subcategoriesData.subcategories.map((subcategory) => {
          subcategory.id = subcategory._id;
          return subcategory;
      });
    }));
  }
}
